import {useState} from 'react';
import {Switch, Route} from 'react-router-dom';
import SideHeader from './SideHeader';
import Header from './Header';
import Booking from './ContactDisplay/ContactApi';
import Employees from './Employess/EmployessApi';



const SigninHome =({handelLogged}) => {

    const [isOpen, setIsOpen] = useState(false)

    const toggle = () => {
        setIsOpen(!isOpen)
    }

    return(
        <div>
            <SideHeader isOpen={isOpen} toggle={toggle} isLogged={handelLogged}/>
            <Header  toggle={toggle} isLogged={handelLogged}/>
            <Switch>
                <Route exact path='/signin/messages' component ={Booking}/>
                <Route exact path='/signin' component ={Employees}/>
            </Switch>
        </div>
    )
}

export default SigninHome;
