import React from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import Home from './pages';
import NoMatch from './pages/404page/404page';
import './App.css';
import Signin from './components/Signin/index';
import SigninHome from './components/SigninPages/SigninHome';

class App extends React.Component{
  state={
    isLog: false
  }

  handelLogin = (isLog) => this.setState({isLog})
  render(){
    const {isLog} = this.state;
    return (
      <Router>
        <Switch>
          <Route path='/' component={Home} exact />
          {
            !isLog ?
            <Route exact path='/signin' render={() => <Signin isLogin={this.handelLogin}/>}/>
            :
            <Route path='/signin' render={() => <SigninHome handelLogged={this.handelLogin}/>}/>
          }
          <Route path='*' component={NoMatch}/>
        </Switch>
      </Router>
    );
  }
}




export default App;
