import React from 'react';
import { withRouter } from 'react-router-dom';
import { SidebarContainer, Icon, CloseIcon, SidebarWrapper, SidebarRoute, SidebarMenu, SidebarLinks, SideBtnWrap } from './SidebarElements';

const SideHeader = ({isOpen, toggle, history, isLogged}) => {

    const handelClick=() =>{
        history.push('/signin')
        isLogged(false)
    }

    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon />
            </Icon>
            <SidebarWrapper>
                <SidebarMenu>
                    <SidebarLinks to="/signin" onClick={toggle}>
                        Employees
                    </SidebarLinks>
                    <SidebarLinks to="/signin/messages" onClick={toggle}>
                        Messages
                    </SidebarLinks>
                </SidebarMenu>
                <SideBtnWrap>
                    <SidebarRoute onClick={(toggle) (handelClick)}>
                        Sign Out
                    </SidebarRoute>
                </SideBtnWrap>
            </SidebarWrapper>
        </SidebarContainer>
    )
}

export default withRouter(SideHeader);
