import React,{Component} from 'react';
import axios from 'axios';
import Employeedb from './index';

const employeeurl = "/api/employees";


class Employee extends Component{
    constructor(){
        super()

        this.state={
            orders:''
        }
    }

    render(){
        console.log("class",this.props)
        return(
            <div className="container">
                <Employeedb orderdata={this.state.orders}/>
            </div>
        )
    }


    async componentDidMount(){
        let response =  await axios.get(`${employeeurl}`)
        this.setState({orders:response.data})
    }


    /*componentDidMount(){
        axios.get(`${bookurl}`)
        .then((response) =>  {this.setState({hotelist:response.data})})
    }*/

}

export default Employee;