import React from 'react';
import {Container, FormWrap, Icon, FormContent, Form, FormH1, FormLabel, FormInput, FormButton} from './SigninElements'

class SignIn extends React.Component {
    state={
        email:'',
        password:''
    }

    handelChange = (e) =>{
        const {name,value} = e.target
        this.setState({[name]:value})
    }

    handelSubmit = (e) =>{
        e.preventDefault()
        this.props.isLogin(true)
    }
    render(){
        return (
                <Container>
                    <FormWrap>
                        <Icon to="/">Solvency</Icon>
                        <FormContent>
                            <Form onSubmit ={this.handelSubmit}>
                                <FormH1>Sign in to your account</FormH1>
                                <FormLabel htmlFor='for'>Email</FormLabel>
                                <FormInput type='email' name='email' required  placeholder='Enter Your Email' onChange={this.handelChange}/>
                                <FormLabel htmlFor='for'>Password</FormLabel>
                                <FormInput type='password' name='password' required  placeholder='Enter Your Password' onChange={this.handelChange} />
                                <FormButton type='submit' onSubmit={this.handelSubmit}>SignIn</FormButton>
                            </Form>
                        </FormContent>
                    </FormWrap>
                </Container>
        )
    }
}

export default SignIn
